"use client";

import { useEffect, useState } from "react";
import useAuthStore from "@/app/store/useAuthStore";
import ProtectedRoute from "@/app/components/ProtectedRoute";
import { User, Heart, Clock, Settings } from "lucide-react";
import { api } from "@/lib/api";
import PropertyList from "@/app/components/PropertyList";

export default function UserDashboardPage() {
  const { user } = useAuthStore();
  const [favoriteProperties, setFavoriteProperties] = useState([]);
  const [recentProperties, setRecentProperties] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    fetchDashboardData();
  }, []);

  const fetchDashboardData = async () => {
    try {
      setLoading(true);
      // Fetch favorite properties
      const favoritesResponse = await api.get("/properties/favorites/all");
      setFavoriteProperties(favoritesResponse.data.data);

      // Fetch recent properties
      const recentResponse = await api.get("/properties", {
        params: { limit: 6, status: "published", sort: "-createdAt" },
      });
      setRecentProperties(recentResponse.data.data);
    } catch (error) {
      console.error("Error fetching dashboard data:", error);
    } finally {
      setLoading(false);
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-[400px]">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-600 mx-auto"></div>
          <p className="mt-4 text-gray-600">Loading dashboard...</p>
        </div>
      </div>
    );
  }

  return (
    <ProtectedRoute allowedRoles={["user"]}>
      <div className="max-w-7xl mx-auto">
        {/* Welcome Section */}
        <div className="mb-8">
          <h1 className="text-3xl font-bold text-gray-900 mb-2">
            Welcome back, {user?.name}!
          </h1>
          <p className="text-gray-600">Here's your personal dashboard</p>
        </div>

        {/* Stats Cards */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6 mb-8">
          <div className="card p-6">
            <div className="flex items-center">
              <div className="p-3 rounded-lg bg-primary-100">
                <User className="w-6 h-6 text-primary-600" />
              </div>
              <div className="ml-4">
                <p className="text-sm text-gray-600">Account Type</p>
                <p className="text-lg font-semibold capitalize">{user?.role}</p>
              </div>
            </div>
          </div>

          <div className="card p-6">
            <div className="flex items-center">
              <div className="p-3 rounded-lg bg-pink-100">
                <Heart className="w-6 h-6 text-pink-600" />
              </div>
              <div className="ml-4">
                <p className="text-sm text-gray-600">Favorites</p>
                <p className="text-lg font-semibold">
                  {user?.favorites?.length || 0}
                </p>
              </div>
            </div>
          </div>

          <div className="card p-6">
            <div className="flex items-center">
              <div className="p-3 rounded-lg bg-yellow-100">
                <Settings className="w-6 h-6 text-yellow-600" />
              </div>
              <div className="ml-4">
                <p className="text-sm text-gray-600">Account Status</p>
                <p className="text-lg font-semibold text-green-600">Active</p>
              </div>
            </div>
          </div>
        </div>

        {/* Favorites Section */}
        <div className="mb-8">
          <div className="flex items-center justify-between mb-6">
            <h2 className="text-2xl font-bold text-gray-900">Your Favorites</h2>
            <Clock className="w-5 h-5 text-gray-400" />
          </div>
          {favoriteProperties.length > 0 ? (
            <PropertyList properties={favoriteProperties} />
          ) : (
            <div className="card p-12 text-center">
              <Heart className="w-12 h-12 text-gray-300 mx-auto mb-4" />
              <h3 className="text-lg font-semibold text-gray-900 mb-2">
                No favorites yet
              </h3>
              <p className="text-gray-600">
                Start exploring properties and add them to your favorites!
              </p>
            </div>
          )}
        </div>

        {/* Recent Properties */}
        <div>
          <h2 className="text-2xl font-bold text-gray-900 mb-6">
            Recently Viewed
          </h2>
          <PropertyList properties={recentProperties} />
        </div>
      </div>
    </ProtectedRoute>
  );
}
